.dialogs {
    background-color: black;
    display: grid;
    grid-template-areas:
            "h h"
            "u m"
            "f f";
    grid-template-columns: .3fr 1fr;
    grid-gap: 10px;
}

.header {
    background-color: rgba(0,0,0,.0);
    text-align: center;
    border-bottom: 2px solid #44475a;
    grid-area: h;
}

.users {
    grid-area: u;
    padding: 0;
    margin: 0;
}

.footer__messages {
    grid-area: f;
}

.roundImage {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    margin-right: 15px;
}

.messages {
    grid-area: m;
    display: flex;
    padding: 10px;
    border-left: 2px solid #44475a;
    flex-direction: column;
    align-items: flex-start;
}