.message {
    margin: 15px;
    padding: 10px;
    background: linear-gradient(0deg, rgb(138, 43, 226), rgb(160, 65, 260));
    box-shadow: 0px 20px 20px -20px rgba(253, 253, 253, 0.5) inset;
    border-radius: 15px;
}

.message > div:first-child {
    text-align: right;
    border-bottom: 1px solid aqua;
    padding-bottom: 5px;
    font-weight: lighter;
    font-size: 0.8em;
}

.myMessage {
    background: linear-gradient(0deg, rgb(16, 16, 21), rgb(28, 28, 34));
    align-self: flex-end;
}