.inputArea {
    grid-area: inputArea;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin: 15px 0;
}

.ta{
    font-size: 18px;
    background-color: rgba(0,0,0,.0);
    color: aqua;
    border: 1px solid orange;
    width: 100%;
    border-radius: 7px;
    /*margin-right: 15px;*/
}

.ta:focus {
    outline: none;
    /*cursor: pointer;*/
}

.myButton {
    /*margin: 16px;*/
    /*padding: 10px 20px;*/
    /*height: 71px;*/
    padding: 15px;
    font-weight: bolder;
    border-radius: 7px;
    border: 1px solid orange;
    color: aqua;
    background-color: rgba(0,0,0,.0);
}

.myButton:hover {
    background-color: orange;
}
