.user {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 0;
    border-bottom: 2px solid #44475a;
}

.user:hover {
    font-weight: bolder;
    background-color: #44475a;
    padding: 10px 5px;
}

.roundImage {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    margin-right: 15px;
}

a {
    text-decoration: none;
    color: aqua;
}

.selected {
    font-weight: bolder;
    color: orange;
}

.selected .user {
    border-bottom: 2px solid orange;
}